<template>
	<view class="union-tabs">
		<u-tabs :list="tabs" :current="current" :scrollable="false"
			:activeStyle="{color:'#1A7CF9 ',fontWeight:'bold',fontSize:'28rpx'}"
			:inactiveStyle="{color:'#858E9A',fontSize:'28rpx'}" itemStyle="padding: 0;height: 48px;"
			@change="tabChange">
		</u-tabs>
		<view class="monitor" v-if="current == 0">
			<view style="width: 100%;height: 224px;background-color:#fff;border-radius: 4px;"></view>
			<!-- <video id="myVideo" src="https://img.cdn.aliyun.dcloud.net.cn/guide/uniapp/%E7%AC%AC1%E8%AE%B2%EF%BC%88uni-app%E4%BA%A7%E5%93%81%E4%BB%8B%E7%BB%8D%EF%BC%89-%20DCloud%E5%AE%98%E6%96%B9%E8%A7%86%E9%A2%91%E6%95%99%E7%A8%8B@20200317.mp4" style="width: 100%;height: 224px;border-radius: 4px;" autoplay="autoplay" :controls="false" object-fit="fill"></video> -->
			<u-tabs :list="dataControlTabs" :current="controlCurrent" :scrollable="false"
				:activeStyle="{color:'#333333',fontWeight:'bold',fontSize:'32rpx'}"
				:inactiveStyle="{color:'#858E9A',fontWeight:'bold',fontSize:'32rpx'}"
				itemStyle="flex: initial;height: 48px;padding: 0 16px;" @change="dataChange"></u-tabs>
			<view class="tab-box d-flex" v-if="controlCurrent == 0">
				<view class="box-item environment" v-for="(item,i) in environmentList" :key="i">
					<image style="width: 25px;height: 31px;margin-bottom: 16px;" :src="item.img" mode="aspectFit">
					</image>
					<view class="d-flex env-data">
						<view style="font-size: 27rpx;font-weight: bold;">{{item.qualityName}}</view>
						<view style="font-size: 32rpx;color:#1A7CF9;font-weight: bolder;">{{item.quality}}</view>
					</view>
					<view class="unit">单位：{{item.unit}}</view>
				</view>
			</view>
			<view class="tab-box d-flex" v-if="controlCurrent == 1">
				<view class="box-item control" v-for="(item, i) in deviceList" :key="i">
					<image style="width: 60px;height: 68px;" :src="item.icon" mode="aspectFit"></image>
					<view class="control-name">{{item.deviceName}}</view>
					<u-switch v-model="item.status" activeColor="#5ac725" size="16" style="margin-left: 4px;"
						@change="deviceChange(item.deviceId)" @input="deviceInput"></u-switch>
				</view>
			</view>
		</view>
		<view class="strategy" v-if="current == 1">
			<view class="box" v-for="(item, i) in strategyList" :key="i">
				<view class="box-item strategy-item">
					<view class="d-flex space-between" style="margin-bottom: 20px;">
						<view class="equ-name">设备：{{item.deviceName}}</view>
						<u-switch v-model="item.status" activeColor="#07C160" size="26" style="margin-left: 4px;">
						</u-switch>
					</view>
					<view class="conditions d-flex space-between">
						<view class="d-flex">
							<view style="color:#999999">开启条件：</view>
							<view>
								<view class="conditions-list">{{item.openCondition}}</view>
							</view>
						</view>
						<view class="d-flex">
							<u-button text="增加" size="mini" type="info"></u-button>
							<u-button text="删除" size="mini" type="error"></u-button>
						</view>
					</view>
					<view class="conditions d-flex space-between">
						<view class="d-flex">
							<view style="color:#999999">关闭条件：</view>
							<view>
								<view class="conditions-list">{{item.closeCondition}}</view>
							</view>
						</view>
						<view class="d-flex">
							<u-button text="增加" size="mini" type="info"></u-button>
							<u-button text="删除" size="mini" type="error"></u-button>
						</view>
					</view>
				</view>
			</view>
			<view>
				<u-empty mode="data" icon="http://cdn.uviewui.com/uview/empty/data.png">
					<u-button size="small" type="primary" text="添加"></u-button>
				</u-empty>
			</view>
		</view>
		<view class="inspection" v-if="current == 2">
			<view class="box" v-for="(item, i) in inspectionList" :key="i">
				<view class="box-item">
					<view class="d-flex space-between">
						<view>设备：{{item.name}}</view>
						<view class="d-flex">
							<u-button text="修改" size="mini" type="primary"></u-button>
							<u-button text="关闭" size="mini" type="error"></u-button>
						</view>
					</view>
					<view class="d-flex space-between" style="margin-top: 26px;">
						<view style="font-size: 28rpx;">
							<text style="color:#999999;margin-right: 10px;">巡检周期</text>
							<text>{{item.time}}</text>
						</view>
						<view :class="item.status == 1 ? 'blue' : 'grey'">{{item.statusInfo}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="log" v-if="current == 3">
			<view class="log-list-top d-flex">
				<view style="width: 65px;">设备</view>
				<view style="width: 65px;">操作类型</view>
				<view style="width: 65px;">操作时间</view>
				<view style="width: 65px;">操作结果</view>
				<view style="width: 48px;">操作人</view>
			</view>
			<view class="log-list-content">
				<u-list @scrolltolower="scrolltolower" height="auto">
					<u-list-item v-for="(item, i) in tableList" :key="i">
						<view class="log-list-item d-flex" :class="item.results == 0 ? 'red' : ''">
							<view style="width: 65px;">{{item.name}}</view>
							<view style="width: 65px;">{{item.type}}</view>
							<view style="width: 65px;">{{item.createdTime}}</view>
							<view style="width: 65px;">{{item.resultsName}}</view>
							<view style="width: 48px;">{{item.personnel}}</view>
						</view>
					</u-list-item>
				</u-list>
			</view>
		</view>
		<view class="log" v-if="current == 4">
			<view class="log-list-top d-flex">
				<view style="width: 30%;">告警时间</view>
				<view style="width: 30%;">告警类型</view>
				<view style="width: 30%;">操作时间</view>
			</view>
			<view class="log-list-content">
				<u-list @scrolltolower="scrolltolower" height="auto">
					<u-list-item v-for="(item, i) in alarmList" :key="i">
						<view class="log-list-item d-flex">
							<view style="width: 30%;">{{item.alarmTime}}</view>
							<view style="width: 40%;">{{item.alarmType}}</view>
							<view style="width: 30%;">{{item.createdTime}}</view>
						</view>
					</u-list-item>
				</u-list>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		updateStatus,
		findStrategy
	} from '@/api/union'
	export default {
		props: {
			deviceList: Array
		},
		data() {
			return {
				tabs: [{
					name: '实时监控'
				}, {
					name: '控制策略'
				}, {
					name: '巡检策略'
				}, {
					name: '操作日志'
				}, {
					name: '告警记录'
				}],
				current: 0,
				dataControlTabs: [{
					name: '实时数据'
				}, {
					name: '设备控制'
				}],
				controlCurrent: 0,
				environmentList: [{
					img: require("../../static/water-quality.png"),
					qualityName: '水质',
					quality: '32',
					unit: '℃'
				}, {
					img: require("../../static/oxygen.png"),
					qualityName: '溶解氧',
					quality: '27',
					unit: '%'
				}, {
					img: require("../../static/water-quality.png"),
					qualityName: '水质',
					quality: '32',
					unit: '℃'
				}],
				deviceStatus: "", //设备控制开关
				strategyList: [], //控制策略数据
				inspectionList: [{
					name: '增氧机1',
					time: '12小时',
					status: 1,
					statusInfo: '启动中'
				}, {
					name: '增氧机2',
					time: '6小时',
					status: 0,
					statusInfo: '未启动'
				}],
				tableList: [{
					name: '增氧机1',
					type: '开启',
					createdTime: '2021.12.28',
					resultsName: '成功',
					results: 1,
					personnel: '用户1'
				}, {
					name: '增氧机2',
					type: '开启',
					createdTime: '2021.12.28',
					resultsName: '成功',
					results: 1,
					personnel: '用户1'
				}, {
					name: '增氧机3',
					type: '开启',
					createdTime: '2021.12.28',
					resultsName: '成功',
					results: 1,
					personnel: '用户1'
				}, {
					name: '增氧机4',
					type: '开启',
					createdTime: '2021.12.28',
					resultsName: '不成功',
					results: 0,
					personnel: '用户1'
				}],
				alarmList: [{
					alarmTime: '2021.12.28',
					alarmType: '设备异常',
					createdTime: '2021.12.28'
				}, {
					alarmTime: '2021.12.28',
					alarmType: '设备异常',
					createdTime: '2021.12.28'
				}, {
					alarmTime: '2021.12.28',
					alarmType: '设备异常',
					createdTime: '2021.12.28'
				}]
			}
		},
		options: {
			styleIsolation: 'shared'
		},
		methods: {
			scrolltolower() {
				this.loadmore()
			},
			loadmore() {
				for (let i = 0; i < 10; i++) {
					this.indexList.push({
						url: this.urls[uni.$u.random(0, this.urls.length - 1)]
					})
				}
			},
			deviceChange(id) {
				updateStatus({
					deviceId: id,
					status: this.deviceStatus
				}).then(res => {
					if (res.code == 0) {
						uni.showToast({
							icon: 'success',
							duration: 2000,
							title: `操作成功`
						});
						this.$parent.loadData();
					} else {
						uni.showToast({
							icon: 'error',
							duration: 2000,
							title: `操作失败`
						});
					}
				})
			},
			deviceInput(value) {
				this.deviceStatus = value === true ? '1' : '2'
			},
			tabChange(e) {
				this.current = e.index || 0
				if (this.current == 1) {
					findStrategy().then(res => {
						this.strategyList = res.data
						this.strategyList.forEach((value, index) => {
							value["status"] = value["status"] == 1 ? true : false
						})
					})
				}
			},
			dataChange(e) {
				this.controlCurrent = e.index || 0
			}
		}
	}
</script>

<style lang="less">
	@import url("../../style.less");

	.union-tabs {
		font-family: 'PingFangSC-Medium' !important;

		.d-flex {
			display: flex;
		}

		.space-between {
			justify-content: space-between;
		}

		.monitor {
			margin: 16px 0;
		}

		.tab-box {
			flex-wrap: wrap;
			justify-content: space-between;
			flex-direction: row;

			.box-item {
				margin-top: 16px;
				background-color: @background-color-white;
				border-radius: 8px;
				width: 22%;

				&:last-child:nth-child(3n + 2) {
					margin-right: calc((100% - 31%) / 2);
				}
			}

			.environment {
				padding: 10px 14px;

				.env-data {
					align-items: center;
					justify-content: space-between;
				}

				.unit {
					margin-top: 6px;
					font-size: 23rpx;
					color: #BBBBBB;
				}
			}

			.control {
				padding: 4px 20px 6px 10px;

				.control-name {
					font-weight: bold;
					font-size: 27rpx;
					margin-bottom: 8px;
				}
			}
		}

		.box {
			margin: 12px 0;
			border-radius: 8px;
			background-color: #fff;

			.box-item {
				padding: 20px 12px;
			}
		}
        .u-empty__wrap{
			margin-top: 20px;
		}
		.strategy-item {
			.equ-name {
				font-weight: bold;
			}

			.conditions {
				font-size: 28rpx;

				.conditions-list {
					margin-bottom: 16px;
				}
			}

			.u-button--info {
				background-color: #E3EFFE;
				color: #1A7CF9;
				border: none;
				margin-right: 10px;
			}

			.u-button--error {
				background-color: #FFE5E9;
				color: #FF2442;
				border: none;
			}

			uni-button:after {
				border: 0;
			}
		}

		.inspection {
			.u-button--primary {
				margin-right: 10px;
			}

			.u-button--mini {
				padding: 12px 0;
			}
		}

		.blue {
			color: @blue;
		}

		.red {
			color: @red  !important;
		}

		.grey {
			color: @grey;
		}

		.log {
			background-color: @font-white;
			margin-top: 16px;
			border-radius: 8px;
			font-size: 28rpx;

			.log-list-top {
				text-align: center;
				align-items: center;
				justify-content: space-around;
				padding: 10px 0;
				border-top-left-radius: 8px;
				border-top-right-radius: 8px;
				background-color: @blue;
				color: @font-white;
				font-weight: bold;
			}

			.log-list-item {
				padding: 10px 8px;
				align-items: center;
				justify-content: space-between;
				text-align: center;
			}
		}

		.bold {
			font-weight: 500;
		}
	}
</style>
